body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  padding-bottom: ;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* adds text color to body text */
body {
  color: grey;
  font-size: 10pt;
}

h1,
h2,
h3,
h4,
h5 {
  color: #2e645a;
  font-family: "Julius Sans One", sans-serif;
}

/* adds margins on all four sides of paragraph text, updates font */
p,
a {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}

a {
  color: orange;
}

/* adds a top margin to all rows  */
.row {
  margin-top: 30px;
}

.card-title {
  color: #2e645a;
}

.brand-logo {
  margin-left: 10px;
}

.nav-wrapper {
  background-color: #2e645a;
}

/* specifies the size of images in cards  */
.card-imgage {
  width: 300px;
}

/* adds margins to form content  */
.form-group {
  margin: 20px 20px 20px 20px;
}

.headshot {
  max-width: 100%;
}

@media only screen and (max-width: 600px) {
  .headshot {
    width: 100%;
  }
}
